*{
    list-style: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: black;
    overscroll-behavior: none;
}
#content{
    /* max-width: 1440px; */
    height: 100vh;
    /* background-color: rgb(0, 255, 157); */
    color: aliceblue;
    padding: 15px;
}
.outer-todo-container,.todo-container{
    height: 100%;
}
.todo-container{
    /* padding-inline-start: 15px; */
    position: relative;
    display: flex;
    flex-direction: column;
}
.main-content-container{
    display: flex;
    height: 100%;
    position: relative;
}
.task-edit-and-add-container{
    position: relative;
    flex: 1 1 100%;
}
.add-task-container{
    position: absolute;
    /* inset: 100% 50% 0 50%; */
    bottom: 0;
    bottom: 0;
    right: 0;
    left: 0;
    display: flex;
    justify-content: center;
}
.todo-navigation-container{
    color: aliceblue;
    background-color: black;
    position: sticky;
    margin-left: 15px;
    height: 100%;
    left: 0;
    width: 120px;
    flex-shrink: 0;
}
.todo-navbar-container{
    display: flex;
    background-color: black;
    gap: 20px;
}
.navigation-link-container{
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 20px;
}
.projects-list-container{
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-left: 10px;
}
.nav-projects-heading{
    display: flex;
    justify-content: space-between;
}
